/**
 * =============================================================================
 * ************   switch 单选框   ************
 * =============================================================================
 */

.mdui-switch {
  position: relative;
  display: inline-block;
  height: 36PX;
  padding-left: 36PX;
  line-height: 36PX;
  cursor: pointer;
  user-select: none;
  // 隐藏系统 input 标签，用自定义的图标代替
  switch,
  [type="checkbox"] {
    appearance: inherit;
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    opacity: 0;
  }
}

/* 图标轨道 */
.mdui-switch-icon {
  position: relative;
  display: inline-block;
  width: 36PX;
  height: 14PX;
  vertical-align: middle;
  background-color: rgba(0, 0, 0, 0.38);
  border-radius: 14PX;
  transition: background-color .3s @animation-curve-default;

  // 圆形图标
  &::before {
    position: absolute;
    top: -3PX;
    left: -3PX;
    display: inline-block;
    width: 20PX;
    height: 20PX;
    background-color: @color-grey-50;
    border-radius: 20PX;
    transition: box-shadow .14s @animation-curve-default, background-color .3s @animation-curve-default, left .3s @animation-curve-default;
    transition-property: unset;
    content: ' ';
    .mdui-shadow(1);
  }
}

/* 选中状态的图标 */
.mdui-switch-checked .mdui-switch-icon, //新增类
.mdui-switch [type="checkbox"]:checked + .mdui-switch-icon,
.mdui-switch switch[aria-checked="true"] + .mdui-switch-icon {
  background-color: rgba(red(@color-default-a200), green(@color-default-a200), blue(@color-default-a200), 0.5);

  &::before {
    left: 20PX;
    background-color: @color-default-a200;
  }
}

/* 未选中时或禁用时 按下的阴影 */
.mdui-switch:active switch,
.mdui-switch switch:focus,
.mdui-switch:active [type="checkbox"],
.mdui-switch [type="checkbox"]:focus {
  & + .mdui-switch-icon::before {
    box-shadow:
      // box-shadow(1)
      0 2px 1px -1px rgba(0, 0, 0, .2),
      0 1px 1px 0 rgba(0, 0, 0, .14),
      0 1px 3px 0 rgba(0, 0, 0, .12),

        // 颜色阴影
      0 0 0 15px rgba(0, 0, 0, 0.1);
  }
}
//新增类
/* 未选中时或禁用时 按下的阴影 */
.mdui-switch-focus-disabled::before,
.mdui-switch-focus::before{
    box-shadow:
      // box-shadow(1)
      0 2px 1px -1px rgba(0, 0, 0, .2),
      0 1px 1px 0 rgba(0, 0, 0, .14),
      0 1px 3px 0 rgba(0, 0, 0, .12),

        // 颜色阴影
      0 0 0 15px rgba(0, 0, 0, 0.1);

}
//新增类
/* 已选中时按下的阴影 */
.mdui-switch-checked .mdui-switch-focus::before{
  box-shadow:
    // box-shadow(1)
    0 2px 1px -1px rgba(0, 0, 0, .2),
    0 1px 1px 0 rgba(0, 0, 0, .14),
    0 1px 3px 0 rgba(0, 0, 0, .12),

      // 颜色阴影
    0 0 0 15px rgba(red(@color-default-a200), green(@color-default-a200), blue(@color-default-a200), 0.16);
}
/* 已选中时按下的阴影 */
.mdui-switch:active [type="checkbox"],
.mdui-switch [type="checkbox"]:focus {
  &:checked:not(:disabled) + .mdui-switch-icon::before {
    box-shadow:
      // box-shadow(1)
      0 2px 1px -1px rgba(0, 0, 0, .2),
      0 1px 1px 0 rgba(0, 0, 0, .14),
      0 1px 3px 0 rgba(0, 0, 0, .12),

        // 颜色阴影
      0 0 0 15px rgba(red(@color-default-a200), green(@color-default-a200), blue(@color-default-a200), 0.16);
  }
}

/* 已选中时按下的阴影 */
.mdui-switch:active switch,
.mdui-switch switch:focus {
  &[aria-checked="true"]:not(:disabled) + .mdui-switch-icon::before {
    box-shadow:
      // box-shadow(1)
      0 2PX 1PX -1PX rgba(0, 0, 0, .2),
      0 1PX 1PX 0 rgba(0, 0, 0, .14),
      0 1PX 3PX 0 rgba(0, 0, 0, .12),

        // 颜色阴影
      0 0 0 15PX rgba(red(@color-default-a200), green(@color-default-a200), blue(@color-default-a200), 0.16);
  }
}

/* 禁用状态 */
.mdui-switch [type="checkbox"]:disabled + .mdui-switch-icon,
.mdui-switch switch[aria-disabled="true"] + .mdui-switch-icon {
  background-color: rgba(0, 0, 0, 0.12) !important;

  &::before {
    background-color: @color-grey-400 !important;
  }
}

//新增类
/* 禁用状态 */
.mdui-switch-disabled .mdui-switch-icon {
  background-color: rgba(0, 0, 0, 0.12) !important;

  &::before {
    background-color: @color-grey-400 !important;
  }
}

/**
 * =============================================================================
 * ************   switch 强调色   ************
 * =============================================================================
 */

& {
  .loop-accent-theme(@counter-color) when (@counter-color > 0) {
    .loop-accent-theme((@counter-color - 1));
    @colorName: extract(@globalAccentColors, @counter-color);

    @color: 'color-@{colorName}-a200';

    .mdui-theme-accent-@{colorName} when not (@colorName = null) {

      //新增类
      // 选中状态的图标
      .mdui-switch-checked .mdui-switch-icon,
      // 选中状态的图标
      .mdui-switch [type="checkbox"]:checked + .mdui-switch-icon,
      .mdui-switch switch[aria-checked="true"] + .mdui-switch-icon {
        background-color: rgba(red(@@color), green(@@color), blue(@@color), 0.5);

        &::before {
          background-color: @@color;
        }
      }

      // 已选中时按下的阴影
      .mdui-switch:active [type="checkbox"],
      .mdui-switch [type="checkbox"]:focus,
      .mdui-switch:active switch,
      .mdui-switch switch:focus {
        &[aria-checked="true"]:not(:disabled) + .mdui-switch-icon::before {
          box-shadow:
            // box-shadow(1)
            0 2PX 1PX -1PX rgba(0, 0, 0, .2),
            0 1PX 1PX 0 rgba(0, 0, 0, .14),
            0 1PX 3PX 0 rgba(0, 0, 0, .12),

              // 颜色阴影
            0 0 0 15PX rgba(red(@@color), green(@@color), blue(@@color), 0.16);
        }
      }
      //新增类
      .mdui-switch-checked .mdui-switch-focus::before{
        box-shadow:
          // box-shadow(1)
          0 2PX 1PX -1PX rgba(0, 0, 0, .2),
          0 1PX 1PX 0 rgba(0, 0, 0, .14),
          0 1PX 3PX 0 rgba(0, 0, 0, .12),

            // 颜色阴影
          0 0 0 15PX rgba(red(@@color), green(@@color), blue(@@color), 0.16);
      }

    }
  }
  .loop-accent-theme(length(@globalAccentColors));
}


/**
 * =============================================================================
 * ************   Radio dark   ************
 * =============================================================================
 */
.layout-theme({
  // 默认状态
  .mdui-switch-icon {
    background-color: rgba(255, 255, 255, 0.3);

    &::before {
      background-color: @color-grey-400;
    }
  }

  // 选中状态
  & {
    .loop-accent-theme(@counter-color) when (@counter-color > 0) {
      .loop-accent-theme((@counter-color - 1));
      @colorName: extract(@globalAccentColors, @counter-color);

      @color: 'color-@{colorName}-a200';
      @color-a100: 'color-@{colorName}-a100';

      &.mdui-theme-accent-@{colorName} when not (@colorName = null) {
        .mdui-switch-checked .mdui-switch-icon,//新增类
        .mdui-switch [type="checkbox"]:checked + .mdui-switch-icon,
        .mdui-switch switch[aria-checked="true"] + .mdui-switch-icon {
          background-color: rgba(red(@@color-a100), green(@@color-a100), blue(@@color-a100), 0.5);

          &::before {
            background-color: @@color-a100;
          }
        }
      }
    }
    .loop-accent-theme(length(@globalAccentColors));
  }

  // 未选中或禁用时 按下的阴影
  .mdui-switch:active [type="checkbox"],
  .mdui-switch [type="checkbox"]:focus,
  .mdui-switch:active switch,
  .mdui-switch switch:focus {
    & + .mdui-switch-icon::before {
      box-shadow:
        // box-shadow(1)
        0 2PX 1PX -1PX rgba(0, 0, 0, .2),
        0 1PX 1PX 0 rgba(0, 0, 0, .14),
        0 1PX 3PX 0 rgba(0, 0, 0, .12),

          // 颜色阴影
        0 0 0 15PX rgba(255, 255, 255, 0.1);
    }
  }
  //新增类 未选中或禁用时 按下的阴影
  .mdui-switch-focus::before{
    box-shadow:
      // box-shadow(1)
      0 2PX 1PX -1PX rgba(0, 0, 0, .2),
      0 1PX 1PX 0 rgba(0, 0, 0, .14),
      0 1PX 3PX 0 rgba(0, 0, 0, .12),

        // 颜色阴影
      0 0 0 15PX rgba(255, 255, 255, 0.1);
  }

  // 禁用状态
  .mdui-switch-disabled .mdui-switch-icon, //新增类
  .mdui-switch [type="checkbox"]:disabled + .mdui-switch-icon,
  .mdui-switch switch[aria-disabled="true"] + .mdui-switch-icon {
    background-color: rgba(255, 255, 255, 0.1) !important;

    &::before {
      background-color: @color-grey-800 !important;
    }
  }
});
