/* 响应式图片和视频 */
/* 小程序 height:auto 有bug fix by lhq */
.mdui-img-fluid {
  //.mdui-img-fluid();
  display: block;
  width: 100%;
}
.mdui-video-fluid {
  //.mdui-img-fluid();
  display: block;
  width: 100%;
}

/* 圆角图片 */
.mdui-img-rounded {
  border-radius: 2PX;
}

/* 圆形图片 */
.mdui-img-circle {
  border-radius: 50%;
}

// embed
.mdui-video-container {
  position: relative;
  height: 0px;
  padding-bottom: 56.25%;
  overflow: hidden;

  iframe,
  object,
  embed {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%
  }
}
