/**
 * =============================================================================
 * ************   radio 单选框   ************
 * =============================================================================
 */

.mdui-radio {
  position: relative;
  display: inline-block;
  height: 36PX;
  padding-left: 36PX;
  line-height: 36PX;
  cursor: pointer;
  user-select: none;

  // 隐藏系统 input 标签，用自定义的图标代替
  [type='radio'],
  radio {
    position: absolute;
    width: 0;
    height: 0;
    overflow: hidden;
    opacity: 0;
  }
}

/* 图标外圈 */
.mdui-radio-icon {
  position: absolute;
  top: 9PX;
  left: 0;
  display: inline-block;
  box-sizing: border-box;
  width: 18PX;
  height: 18PX;
  vertical-align: middle;
  border: 2PX solid @color-black-icon;
  border-radius: 18PX;
  transition: all .3s @animation-curve-default,
              box-shadow .14s @animation-curve-default;

  // 图标内部圆圈
  &::before {
    position: absolute;
    top: 0;
    left: 0;
    width: 14PX;
    height: 14PX;
    background-color: @color-default-a200;
    border-radius: 14PX;
    transform: scale(0);
    opacity: 0;
    transition: all .3s @animation-curve-default;
    content: ' ';
  }
}

/* 选中状态的图标 */
.mdui-radio-checked .mdui-radio-icon,
.mdui-radio [type="radio"]:checked + .mdui-radio-icon,
.mdui-radio radio[aria-checked="true"] + .mdui-radio-icon {
  border-color: @color-default-a200;

  &::before {
    transform: scale(0.68);
    opacity: 1;
  }
}


/* 禁用状态的图标 */
.mdui-radio .mdui-radio-disabled,
.mdui-radio radio[aria-disabled="true"] + .mdui-radio-icon,
.mdui-radio [type="radio"]:disabled + .mdui-radio-icon {
  border-color: @color-black-icon-disabled !important;
}

/* 禁用且选中状态的图标 */
.mdui-radio-checked .mdui-radio-disabled,
.mdui-radio radio[aria-disabled="true"][aria-checked="true"] + .mdui-radio-icon,
.mdui-radio [type="radio"]:disabled:checked + .mdui-radio-icon {
  &::before {
    background-color: @color-black-icon-disabled !important;
  }
}

.mdui-radio-focus .mdui-radio-active {
  box-shadow: 0 0 0 15PX rgba(red(@color-default-a200), green(@color-default-a200), blue(@color-default-a200), 0.16);
}
/* 未选中、禁用时 按下的阴影 */
.mdui-radio-focus .mdui-radio-disabled {
  box-shadow: 0 0 0 15PX rgba(0, 0, 0, 0.1);
}

.mdui-radio:active radio,
.mdui-radio radio:focus,
.mdui-radio:active [type="radio"],
.mdui-radio [type="radio"]:focus {
  & + .mdui-radio-icon {
    box-shadow: 0 0 0 15PX rgba(0, 0, 0, 0.1);
  }
}

/* 已选中时按下的阴影 */
.mdui-radio:active radio,
.mdui-radio radio:focus,
.mdui-radio:active [type="radio"],
.mdui-radio [type="radio"]:focus {
  &[aria-checked="true"]:not(:disabled) + .mdui-radio-icon,
  &:checked:not(:disabled) + .mdui-radio-icon {
    box-shadow: 0 0 0 15PX rgba(red(@color-default-a200), green(@color-default-a200), blue(@color-default-a200), 0.16);
  }
}

/**
 * =============================================================================
 * ************   radio 强调色   ************
 * =============================================================================
 */

& {
  .loop-accent-theme(@counter-color) when (@counter-color > 0) {
    .loop-accent-theme((@counter-color - 1));
    @colorName: extract(@globalAccentColors, @counter-color);

    @color: 'color-@{colorName}-a200';

    .mdui-theme-accent-@{colorName} when not (@colorName = null) {

      // 选中状态的图标
      .mdui-radio-checked .mdui-radio-active,
      .mdui-radio radio[aria-checked="true"] + .mdui-radio-icon,
      .mdui-radio [type="radio"]:checked + .mdui-radio-icon {
        border-color: @@color;

        &::before {
          background-color: @@color;
        }
      }

      // 已选中时按下的阴影
      .mdui-radio-focus .mdui-radio-active{
        box-shadow: 0 0 0 15PX rgba(red(@@color), green(@@color), blue(@@color), 0.16);
      }
      .mdui-radio:active radio,
      .mdui-radio radio:focus,
      .mdui-radio:active [type="radio"],
      .mdui-radio [type="radio"]:focus {
        &:checked:not(:disabled) + .mdui-radio-icon {
          box-shadow: 0 0 0 15PX rgba(red(@@color), green(@@color), blue(@@color), 0.16);
        }
      }
    }
  }
  .loop-accent-theme(length(@globalAccentColors));
}


/**
 * =============================================================================
 * ************   radio dark   ************
 * =============================================================================
 */
.layout-theme({
  // 图标外圈
  .mdui-radio-icon {
    border-color: rgba(255, 255, 255, 0.7);
  }

  // 禁用状态
  .mdui-radio-disabled,
  .mdui-radio radio[aria-disabled="true"] + .mdui-radio-icon
  .mdui-radio [type="radio"]:disabled + .mdui-radio-icon {
    border-color: @color-white-icon-disabled !important;
  }

  // 禁用且选中状态
  .mdui-radio-checked .mdui-radio-disabled,
  .mdui-radio radio[aria-disabled="true"][aria-checked="true"] + .mdui-radio-icon
  .mdui-radio [type="radio"]:disabled:checked + .mdui-radio-icon {
    &::before {
      background-color: @color-white-icon-disabled !important;
    }
  }

  // 未选中或禁用时 按下的阴影
  .mdui-radio-focus .mdui-radio-active{
    box-shadow: 0 0 0 15PX rgba(255, 255, 255, 0.1);
  }
  .mdui-radio:active radio,
  .mdui-radio radio:focus,
  .mdui-radio:active [type="radio"],
  .mdui-radio [type="radio"]:focus {
    & + .mdui-radio-icon {
      box-shadow: 0 0 0 15PX rgba(255, 255, 255, 0.1);
    }
  }
});
