/**
 * =============================================================================
 * ************   List 列表   ************
 * =============================================================================
 */

/* 列表 */
.mdui-list {
  margin: 0;
  padding: 8PX 0;
  list-style: none;
  background-color: transparent;
  // 子列表没有上下间距
  .mdui-list {
    padding: 0;
  }

  // 分割线上下边距
  &>.mdui-divider,
  &>.mdui-divider-light,
  &>.mdui-divider-dark,
  &>.mdui-divider-inset,
  &>.mdui-divider-inset-light,
  &>.mdui-divider-inset-dark {
    margin-top: 8PX;
    margin-bottom: 8PX;
  }

  a {
    color: inherit;
    text-decoration: none;
  }

  // 使用副标题时自动添加分割线
  .mdui-subheader,
  .mdui-subheader-inset {
    &::before {
      position: absolute;
      right: 0;
      left: 0;
      display: block;
      height: 1PX;
      background-color: @color-black-divider;
      content: ' ';
    }

    margin-top: 8PX;

    &:first-child {
      &::before {
        background-color: transparent;
      }

      // 第一个副标题把列表的 padding-top 抵消掉
      margin-top: -8PX;
    }
  }
  .mdui-subheader-inset {
    &::before {
      left: 72PX;
    }
  }
}

/* 列表项 */
.mdui-list-item {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  min-height: 48PX;
  padding: 0 16PX;
  text-decoration: none;
  cursor: pointer;
  transition: background-color .3s @animation-curve-default;

  &:hover {
    background-color: @color-hover-black;
  }

  // http://stackoverflow.com/a/31915203
  &::after {
    height: 48PX;
    visibility: hidden;
    content: ' ';
  }
}

/* 列表项图标 */
.mdui-list-item-icon {
  width: 24PX;
  min-width: 24PX;
  height: 24PX;
  color: @color-black-icon;
}

/* 列表项头像 */
.mdui-list-item-avatar {
  min-width: 40PX;
  max-width: 40PX;
  height: 40PX;
  margin-top: 8PX;
  margin-bottom: 8PX;
  color: @color-white;
  line-height: 40PX;
  text-align: center;
  background-color: @color-grey-400;
  border-radius: 50%;
  Image,
  img {
    width: 100%;
    height: 100%;
    border-radius: 50%;
  }
}

/* 列表项内容 */
.mdui-list-item-content {
  flex-grow: 1;
  padding-top: 14PX;
  padding-bottom: 14PX;
  font-weight: 400;
  font-size: 16PX;
  line-height: 20PX;
}

/* 列表项内容的副内容 */
.mdui-list-item-text {
  font-size: 14PX;
  opacity: @opacity-black-secondary;
}
.mdui-list-item-title {
  ~ .mdui-list-item-text {
    margin-top: 4PX;
  }
}

/* 激活状态的列表项 */
.mdui-list-item-active {
  font-weight: 700;
  background-color: @color-hover-black;

  .mdui-list-item-content {
    font-weight: 700;
  }
  .mdui-list-item-text {
    font-weight: 400;
  }
}

/* 限制文本高度 */
.mdui-list-item-one-line,
.mdui-list-item-two-line,
.mdui-list-item-three-line {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
}
.mdui-list-item-one-line {
  -webkit-line-clamp: 1;
  height: 20PX;
}
.mdui-list-item-two-line {
  -webkit-line-clamp: 2;
  height: 40PX;
}
.mdui-list-item-three-line {
  -webkit-line-clamp: 3;
  height: 60PX;
}

/* 列表项内的元素间添加间距 */
.mdui-list-item-icon {
  ~ .mdui-list-item-content {
    margin-left: 32PX;
  }
}
.mdui-checkbox,
.mdui-radio,
.mdui-switch {
  ~ .mdui-list-item-content {
    margin-left: 20PX;
  }
}


.mdui-list-item-avatar {
  ~ .mdui-list-item-content {
    margin-left: 16PX;
  }
}
.mdui-list-item-content {
  ~ .mdui-list-item-icon,
  ~ .mdui-list-item-avatar,
  ~ .mdui-checkbox,
  ~ .mdui-radio,
  ~ .mdui-switch {
    margin-left: 16PX;
  }

  ~ .mdui-checkbox,
  ~ .mdui-radio {
    padding-left: 24PX;
  }
}

/* 密集型列表 */
.mdui-list-dense {
  padding: 4PX 0;
  font-size: 13PX;

  &>.mdui-divider,
  &>.mdui-divider-light,
  &>.mdui-divider-dark,
  &>.mdui-divider-inset,
  &>.mdui-divider-inset-light,
  &>.mdui-divider-inset-dark {
    margin-top: 4PX;
    margin-bottom: 4PX;
  }

  // 副标题
  .mdui-subheader,
  .mdui-subheader-inset {
    height: 40PX;
    margin-top: 4PX;
    font-size: 12PX;
    line-height: 40PX;

    &:first-child {
      margin-top: -4PX;
    }
  }

  // 密集型列表项
  .mdui-list-item {
    min-height: 40PX;

    &::after {
      height: 40PX;
    }
  }

  // 图标
  .mdui-list-item-icon {
    width: 20PX;
    height: 20PX;
    font-size: 20PX;
  }

  // 头像
  .mdui-list-item-avatar {
    width: 36PX;
    min-width: 36PX;
    height: 36PX;
    min-height: 36PX;
  }

  // 列表项内容
  .mdui-list-item-content {
    padding-top: 11PX;
    padding-bottom: 11PX;
    font-size: 13PX;
    line-height: 18PX;
  }
  .mdui-list-item-text {
    font-size: 13PX;
  }
  .mdui-list-item-title {
    ~ .mdui-list-item-text {
      margin-top: 2PX;
    }
  }

  // 限制文本高度
  .mdui-list-item-one-line {
    height: 18PX;
  }
  .mdui-list-item-two-line {
    height: 36PX;
  }
  .mdui-list-item-three-line {
    height: 54PX;
  }
}


/**
 * =============================================================================
 * ************   List dark   ************
 * =============================================================================
 */
.layout-theme({
  .mdui-list {
    // 副标题的分割线
    .mdui-subheader,
    .mdui-subheader-inset {
      &::before {
        background-color: @color-white-divider;
      }

      &:first-child {
        &::before {
          background-color: transparent;
        }
      }
    }
  }

  // 列表项
  .mdui-list-item {
    color: @color-white-text;

    &:hover {
      background-color: @color-hover-white;
    }
  }

  // 列表项图标
  .mdui-list-item-icon {
    color: @color-white-icon;
  }

  // 列表内容副内容
  .mdui-list-item-text {
    opacity: @opacity-white-secondary;
  }

  // 激活状态的列表项
  .mdui-list-item-active {
    background-color: @color-hover-white;
  }
});

/**
 * ==============================================================================
 * ************   List inner theme   ************
 * ==============================================================================
 */
[class*="mdui-color-"] {
  .mdui-list-item {
    color: inherit;
  }
  .mdui-list-item-icon {
    color: inherit;
  }
}
