/**
 * =============================================================================
 * ************   Tooltip 工具提示   ************
 * =============================================================================
 */

.mdui-tooltip {
  position: absolute;
  z-index: @z-index-tooltip;
  display: inline-block;
  box-sizing: border-box;
  max-width: 180PX;
  min-height: 32PX;
  padding: 8PX 16PX;
  color: #fff;
  font-weight: 500;

  // 移动端样式
  font-size: 14PX;
  line-height: 22PX;
  text-align: left;
  background-color: rgba(red(@color-grey-700), green(@color-grey-700), blue(@color-grey-700), 0.9);
  border-radius: 2PX;
  transform: scale(0);
  opacity: 0;
  transition-timing-function: @animation-curve-default;
  transition-duration: 0.15s;
  transition-property: opacity, transform;
  will-change: opacity, transform;

  // 桌面端样式
  @media (min-width: @screen-md-min) {
    max-width: 200PX;
    min-height: 24PX;
    padding: 4PX 8PX;
    font-size: 12PX;
    line-height: 18PX;
  }
}

/* 显示工具提示 */
.mdui-tooltip-open {
  transform: scale(1);
  opacity: 1;
}
